.dashboard {
  max-width: 800px;
  margin: 40px auto;
  padding: 24px;
  background: #f8f9fa;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.08);
  font-family: 'Segoe UI', Arial, sans-serif;
}
header {
  text-align: center;
  margin-bottom: 24px;
}
.input-section {
  margin-bottom: 32px;
}
.input-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}
.input-group {
  flex: 1 1 180px;
  display: flex;
  flex-direction: column;
}
.input-group label {
  font-weight: 500;
  margin-bottom: 6px;
}
.input-group input {
  padding: 6px 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 1rem;
}
button {
  margin-top: 18px;
  padding: 10px 24px;
  background: #0057b7;
  color: #fff;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.2s;
}
button:disabled {
  background: #aaa;
  cursor: not-allowed;
}
.result-section {
  margin-top: 32px;
  background: #fff;
  border-radius: 8px;
  padding: 18px;
  box-shadow: 0 1px 6px rgba(0,0,0,0.06);
}
.result-section h2 {
  margin-top: 0;
}
.result-section ul {
  list-style: none;
  padding: 0;
}
.result-section img {
  margin-top: 12px;
  border-radius: 6px;
  box-shadow: 0 1px 6px rgba(0,0,0,0.08);
}
.dashboard{
  max-width:1100px;
  margin: 22px auto;
  padding:28px;
  background: linear-gradient(180deg, rgba(255,255,255,0.03), rgba(255,255,255,0.02));
  border-radius:16px;
  box-shadow: 0 10px 40px rgba(2,6,23,0.6);
  backdrop-filter: blur(6px);
}
header{
  text-align:center;
  margin-bottom:18px;
}
header h1{
  margin:0;
  color: #fff;
  letter-spacing: -0.5px;
}
header p{
  margin:6px 0 0 0;
  color: rgba(255,255,255,0.8);
}
.input-section{
  margin-top:12px;
}
.input-grid{
  display:grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap:16px;
  margin-bottom:12px;
}
.input-group{
  display:flex;
  flex-direction:column;
}
.input-group label{
  font-weight:600;
  margin-bottom:6px;
  color:rgba(255,255,255,0.9);
}
.input-group input{
  padding:10px 12px;
  border-radius:10px;
  border:1px solid rgba(255,255,255,0.08);
  background: rgba(255,255,255,0.02);
  color: #fff;
  outline:none;
  transition: box-shadow .12s, transform .08s;
}
.input-group input:focus{
  box-shadow: 0 6px 18px rgba(79,70,229,0.12);
  transform: translateY(-2px);
}
.primary-btn{
  display:inline-flex;
  align-items:center;
  gap:8px;
  padding:12px 20px;
  background: linear-gradient(90deg, var(--accent), var(--accent-2));
  color:#fff;
  border:none;
  border-radius:12px;
  font-weight:700;
  cursor:pointer;
  box-shadow: 0 8px 30px rgba(79,70,229,0.18);
  transition: transform .12s ease, box-shadow .12s ease;
}
.primary-btn:hover{ transform: translateY(-3px); }
.primary-btn:disabled{ opacity:0.6; cursor:not-allowed; transform:none; }
.result-section{
  margin-top:22px;
  background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
  border-radius:12px;
  padding:18px;
  box-shadow: 0 6px 20px rgba(2,6,23,0.5);
}
.result-grid{
  display:grid;
  grid-template-columns: 1fr 320px;
  gap:18px;
}
@media (max-width:900px){
  .result-grid{ grid-template-columns: 1fr; }
}
.result-section h2{ margin-top:0; color:#fff; }
.result-section ul{ list-style:none; padding:0; color:rgba(255,255,255,0.9); }
.result-section img{ width:100%; border-radius:8px; }

.small-muted{ color: rgba(255,255,255,0.7); font-size:0.95rem; }

.nav-actions{ margin-left:auto; display:flex; gap:8px; }

