/* Dashboard.css - styling for the Dashboard component */
.dashboard {
  max-width: 800px;
  margin: 40px auto;
  padding: 24px;
  background: #f8f9fa;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.08);
  font-family: 'Segoe UI', Arial, sans-serif;
}
header {
  text-align: center;
  margin-bottom: 24px;
}
.input-section {
  margin-bottom: 32px;
}
.input-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}
.input-group {
  flex: 1 1 180px;
  display: flex;
  flex-direction: column;
}
.input-group label {
  font-weight: 500;
  margin-bottom: 6px;
}
.input-group input {
  padding: 6px 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 1rem;
}
button {
  margin-top: 18px;
  padding: 10px 24px;
  background: #0057b7;
  color: #fff;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.2s;
}
button:disabled {
  background: #aaa;
  cursor: not-allowed;
}
.result-section {
  margin-top: 32px;
  background: #fff;
  border-radius: 8px;
  padding: 18px;
  box-shadow: 0 1px 6px rgba(0,0,0,0.06);
}
.result-section h2 {
  margin-top: 0;
}
.result-section ul {
  list-style: none;
  padding: 0;
}
.result-section img {
  margin-top: 12px;
  border-radius: 6px;
  box-shadow: 0 1px 6px rgba(0,0,0,0.08);
}
/* Dashboard.css - styling for the Dashboard component */

@import '../App.css';
/* component-specific tweaks */
.dashboard .input-section{ max-width:980px; margin: 0 auto; }
.dashboard .input-section .input-grid{ margin-top:6px; }
.dashboard .result-section img{ max-height:360px; object-fit:contain; }
.nav-links a{ color:var(--muted); margin-right:12px; text-decoration:none; }
.nav-links a:hover{ color:#fff; }
input, button, .result-section{ transition: all 180ms ease; }

